import MasterPage from '../components/Master';
import fetch from 'isomorphic-unfetch';
const Index = () => (
  <MasterPage>
    <div className="row">
      <div className="col-12">
        <h2>Precio del bitcoin</h2>
      </div>
      <div className="col-md-8">
      <h2>Noticias sobre bitcoin</h2>
      </div>
      <div className="col-md-4">
      <h2>Proximos eventos bitcoin</h2>
      </div>
    </div>
  </MasterPage>
);

Index.getInitialProps = async () => {
  const precio = await fetch('https://pro-api.coinmarketcap.com/v1/cryptocurrency/listings/latest');
  const restPrecio = await precio.json();
  return {
    precioBitcoin: restPrecio
  }
}

export default Index;
